.sailor-day {
  background-color: #FCF3FD;
}

.sailor-day .title-bar {
  background-image: linear-gradient(270deg, #F7C9F3 0%, #4180F6 100%);
}

.sailor-day .title-bar span {
  font-size: 14px;
  color: #FFF;
  font-weight: 700;
}

.sailor-day .title-bar.top-bar {
  background-image: linear-gradient(270deg, #F7C9F3 0%, #4180F6 100%);
  box-shadow: 0 1px 0 0 #110038;
}

.sailor-day .title-bar.top-bar span {
  font-size: 12px;
  color: #FFF;
  font-weight: 400;
}
